import React,{useState,useEffect} from 'react';

function Politic(){
    const [data,setData] = useState([])
    const [query,setQuery] = useState('')
    useEffect(async () => {
        const request = await fetch(`http://servicodados.ibge.gov.br/api/v3/noticias/?qtd=3/?busca=` + query)
        const response = await request.json()
        await setTimeout(() => setData(response.items),1000)
    },[query])
    return(
        <>
        <div className="search">
         <input 
         type='text' 
         placeholder="O que Procura?"
         onChange={e => setQuery(e.target.value)}
         />  
         </div>  
         <div>
        <>
           {data.length? 
              data.map(list =>
         <div className="list-api" key={list.id}>
            <li style={{padding:'20px 10px',listStyleType:'none'}}>
             <a href={list.link} target="_blank">{list.titulo}</a>
             <br/>
             <strong>{list.tipo}: {list.introducao}</strong>
            </li>
            <button>
                <a style={{color:'#000'}} href={list.link}>Ver</a>
            </button>
         </div>) : <p>Carregando...</p>
           }
        </>
    </div>
        </>
    )
}

export default Politic